import React from "react"
import Button from '@mui/material/Button';

class Card extends React.Component{
  constructor(props) {
    super(props);

    this.imageSrc = this.props.imageSrc;
    this.heading = this.props.heading;
    this.paragraph = this.props.paragraph;
    this.price = this.props.price;
    this.color = this.props.color;
    this.textColor = this.props.textColor;

    this.updateAndShowDescription = this.updateAndShowDescription.bind(this);
  }
  
  updateAndShowDescription = () => {
    this.props.changeDetailsFlag(); 
    this.props.sendDescription(this.heading, this.paragraph, this.imageSrc, this.price, this.color, this.textColor);
  }

  render(){
    return (
      <div
        className="card"
        style={{
          backgroundImage: "url(" + this.imageSrc[0].original + ")"
        }}
      >
        <div className="content">
          <h1 className="header">{this.heading}</h1>
          <p className="text">{this.paragraph}</p>
          <h3>{this.price}</h3>
          <Button onClick={this.updateAndShowDescription}>
            Explore
          </Button>
        </div>
      </div>
    )
  }
}

export default Card
