import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
// Components
import Header from "../components/Header"
import Work from "../components/Work"
import Footer from "../components/Footer"
import Modal from "../components/atoms/Modal"

class IndexPage extends React.Component{
  constructor(props) {
    super(props);
   
    this.state = {
      showDetailsFlag: false,
      detailsObject: {}
    };

    this.changeDetailsFlag = this.changeDetailsFlag.bind(this);
    this.sendDescription = this.sendDescription.bind(this);

  }

  changeDetailsFlag() {
    this.setState({
      showDetailsFlag : !this.state.showDetailsFlag
    })
  }

  sendDescription = (title, para, imageSrc, price, color, textColor) => {
    var detailsObject = {
      title: title,
      para: para,
      imageSrc: imageSrc,
      price: price,
      color: color,
      textColor: textColor
    }

    this.setState({
      detailsObject : detailsObject
    })

    console.log(detailsObject);
  }

  showDetails = () => {
    return (
      <Modal showDetails={this.changeDetailsFlag} show={this.state.showDetailsFlag} currentDetailsObject={this.state.detailsObject}/>
    );
  }
    
  render(){
      return (
        <Layout>
          <SEO title="Margo Shale" />
          <Header></Header>
          {this.state.showDetailsFlag ? this.showDetails() : null}
          <Work changeDetailsFlag={this.changeDetailsFlag} sendDescription={this.sendDescription}></Work>
          <Footer></Footer>
        </Layout>
      );
  }
}

export default IndexPage
