import React from "react"
import Card from "./atoms/Card"
import Fade from "react-reveal/Fade"

import data from "../yourdata"

class Work extends React.Component{

  render(){
    return (
      <div className="section">
        <div className="container">         
          <div className="work-wrapper" id="shop">
              <Fade bottom>
                <h1>Shop</h1>
              </Fade>   
              <div className="grid columns-3">
                <Fade bottom>
                  {data.projects.filter(project => project.shop).map((project, index) => (
                    project.shop &&
                    <Card
                      key={index}
                      heading={project.title}
                      paragraph={project.para}
                      imageSrc={project.imageSrc}
                      price={project.price}
                      color={project.color}
                      textColor={project.textColor}
                      changeDetailsFlag={this.props.changeDetailsFlag}
                      sendDescription={this.props.sendDescription}
                    ></Card>
                  ))}
                </Fade>
              </div>
          </div>
          <div className="work-wrapper" id="portfolio">
              <Fade bottom>
                <h1>Portfolio</h1>
              </Fade>
                <div>
                  <div className="work-wrapper-sub">
                    <Fade bottom>
                      <h2>Design</h2>
                    </Fade>
                    <div className="grid columns-3">
                      <Fade bottom>
                        {data.projects.filter(project => !project.shop && project.design).map((project, index) => (
                          <Card
                            key={index}
                            heading={project.title}
                            paragraph={project.para}
                            imageSrc={project.imageSrc}
                            price={project.price}
                            color={project.color}
                            textColor={project.textColor}
                            changeDetailsFlag={this.props.changeDetailsFlag}
                            sendDescription={this.props.sendDescription}
                          ></Card>
                        ))}
                      </Fade>
                    </div>
                  </div>
                  <div className="work-wrapper-sub">
                    <Fade bottom>
                      <h2>Projects</h2>
                    </Fade>
                    <div className="grid columns-1">
                      <Fade bottom>
                          <Card
                            key={0}
                            heading={data.projects_id[0].title}
                            paragraph={data.projects_id[0].para}
                            imageSrc={data.projects_id[0].imageSrc}
                            price={data.projects_id[0].price}
                            color={data.projects_id[0].color}
                            textColor={data.projects_id[0].textColor}
                            changeDetailsFlag={this.props.changeDetailsFlag}
                            sendDescription={this.props.sendDescription}
                          ></Card>
                      </Fade>
                    </div>
                  </div>
                  <div className="work-wrapper-sub">
                    <Fade bottom>
                      <h2>Illustration</h2>
                    </Fade>
                    <div className="grid columns-3">
                      <Fade bottom>
                        {data.projects.filter(project => !project.shop && !project.design).map((project, index) => (
                          <Card
                            key={index}
                            heading={project.title}
                            paragraph={project.para}
                            imageSrc={project.imageSrc}
                            price={project.price}
                            color={project.color}
                            textColor={project.textColor}
                            changeDetailsFlag={this.props.changeDetailsFlag}
                            sendDescription={this.props.sendDescription}
                          ></Card>
                        ))}
                      </Fade>
                    </div>
                  </div>
                </div>
          </div>
        </div>
      </div>
    )
  }
}

export default Work
