import React from "react"
import ReactModal from "react-modal"
import Description from "./Description"
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';

class Modal extends React.Component{
  show = this.props.show;
  showDetails = this.props.showDetails;
  currentDetailsObject = this.props.currentDetailsObject;

  styles = {
    overlay: {
      backgroundColor: 'rgba(0, 0, 0, 0.75)'
    },
    content: {
      top: '10px',
      left: '10px',
      right: '10px',
      bottom: '10px',
      padding: '0px'
    }
  }

  render(){
    return (
      <div>
        <ReactModal
          isOpen={this.show}
          style={this.styles}
        > 
          <div className="content-modal">
            <IconButton aria-label="delete" onClick={this.showDetails}
            style={{
              backgroundColor: this.currentDetailsObject.color
            }} color="primary">
              <CloseIcon
            style={{
              color: this.currentDetailsObject.textColor
            }}
            className="btn-close"/>
            </IconButton>
            
          </div>
          <Description currentDetailsObject={this.currentDetailsObject}/>
        </ReactModal>
      </div>
    );
  }
}

export default Modal
