import React from "react"
import Container from '@mui/material/Container';
import Box from '@mui/material/Box';
import ImageGallery from 'react-image-gallery';

class Description extends React.Component{

    currentDetailsObject = this.props.currentDetailsObject;

    render(){
        return (
            <div className="content-description">
                <div className="img-description">
                    <ImageGallery items={this.currentDetailsObject.imageSrc} showFullscreenButton={false} showPlayButton={false}/>
                </div>
                <div className="text-contents">
                    <div className="text-description">
                        <h1>{this.currentDetailsObject.title}</h1>
                        <h3>Description</h3>
                        <p>{this.currentDetailsObject.para}</p>
                        {this.currentDetailsObject.price && <p>Price includes delivery.</p>}
                        <h2 className="price" style={{
                            backgroundColor: this.currentDetailsObject.color,
                            color: this.currentDetailsObject.textColor
                        }}>
                            {this.currentDetailsObject.price}
                        </h2>
                    </div>
                </div>
            </div>
        );
    }
}

export default Description
